.reasons {
  @include clear-list;

  background-color: #ffffff;
  color: #000000;
  margin-top: 31px;
  margin-bottom: 25px;
  margin-left: 65px;
  margin-right: 18px;
  display: grid;
  grid-template-columns: 25px 1fr;
  row-gap: 14px;

  @include tablet {
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 34px;

    justify-items: center;

    margin: 0;
    padding: 0;
    min-height: 331px;
  }
}

.reasons__item {
  text-align: start;

  padding-bottom: 20px;

  max-width: 237px;

  grid-column: 2 / 3;

  display: grid;
  row-gap: 16px;

  position: relative;

  @include tablet {
    text-align: center;

    grid-column: auto;

    @include flex-center(column);
  }

  @include tablet {
    max-width: 350px;
  }

  &--housing {
    @include tablet {
      row-gap: 25px;
      padding-top: 150px;
      padding-bottom: 55px;
    }

    @include desktop {
      padding-top: 150px;
    }
  }

  &--food {
    @include tablet {
      row-gap: 22px;
      padding-top: 150px;
      padding-bottom: 55px;
    }

    @include desktop {
      padding-top: 150px;
    }
  }

  &--souvenirs {
    @include tablet {
      row-gap: 23px;
      padding-top: 150px;
      padding-bottom: 64px;
    }

    @include desktop {
      padding-top: 165px;
    }
  }
}

.reasons__item--hotels .reasons__text,
.reasons__item--souvenirs .reasons__text {
  @include tablet {
    max-width: 180px;
  }

  @include desktop {
    max-width: 300px;
  }
}

.reasons__item--souvenirs br {
  display: none;

  @include desktop {
    display: inline;
  }
}

.reasons__item--souvenirs .reasons__text {
  @include tablet {
    max-width: 180px;
  }

  @include desktop {
    max-width: 250px;
  }
}

.reasons__item--food .reasons__text {
  @include tablet {
    max-width: 200px;
  }

  @include desktop {
    max-width: 350px;
  }
}

.reasons__item--housing::before,
.reasons__item--food::before,
.reasons__item--souvenirs::before {
  position: absolute;

  content: "";

  width: 40px;
  height: 40px;

  left: -47px;
  top: -8px;

  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;

  @include tablet {
    left: 50%;
    top: 55px;

    transform: translateX(-50%);
  }
}

