.reasons__item .reasons__title {
  margin: 0;

  grid-row: 1 / 2;
}

.reasons__item--souvenirs .reasons__title {
  padding-top: 0;
}

.reasons__title {
  font-size: 18px;
  line-height: 26px;
  margin: 14px 0 11px;

  @include tablet{
    font-size: 21px;
    margin: 14px 0 14px;
  }
}
